import React, { useContext } from 'react';
import logo from '../img/utl.png';
import logo2 from '../img/Tics.png';
import { useHistory } from 'react-router';
import { AuthContext } from '../context/AuthContext';
import { authTypes } from '../types/authTypes';

const Inicio = () => {

    const { dispatch } = useContext(AuthContext);
    const history = useHistory();

    const handleLogin = () => {

        dispatch({type: authTypes.login});

        history.push("/Participantes");
    }



    return (
        <>
        <header >
            <br></br>
            <br></br>
            <br></br>
            <br></br>
            <br></br>
        </header>
        <body>
          <div className="container" >
            <div className="row">        
              <div className="col-6 ms-auto text-center"  >
                <img src={logo} className="" alt="logo"  /> 
                <br></br> <br></br> 
                <h3 className="text-center" >Congreso de <br></br> Tecnologías de la Información</h3>
    
                <div className="d-grid gap-2 col-2 mx-auto"> 
                  <button type="submit" className="btn btn-primary" onClick={handleLogin}   >
                    Entrar 
                  </button>
                </div>              
              </div>
      
              <div className="col-6 ms-auto" >
               <img src={logo2} className="" alt=""  />             
               </div>
            </div>        
          </div>
        </body>
        </>
    );
};

export default Inicio;
