import React, {useContext} from 'react';
import { NavLink, useHistory } from 'react-router-dom';
import { AuthContext } from '../context/AuthContext';
import { authTypes } from '../types/authTypes';
import logo2 from '../img/Tics.png';
import logo from '../img/utl.png';
import './Estilos.css';

const Navbar = () => {

    //Recuperamos el context y el dispatch para moderlo modificar.
    const { dispatch } = useContext(AuthContext);


    //Definimos el history usando el hook useHistory
    const history = useHistory();

    const handleLogout = () => {
       // console.log(history);

       //Usamos el dispatch para modificar el context y establecer log=true.
      dispatch({type: authTypes.logout});

       //Usando el método replace del history para reemplazar una ruta por otra.
       history.replace("/login");
    };

    return (
        <nav className="navbar navbar-expand-lg navbar-light bg-light" >
           
            <div className="container-fluid">
                <img  src={logo2} className="logo" />

                <a className="navbar-brand" href="/"></a>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                <div className="collapse navbar-collapse" id="navbarNav">
                    <ul className="navbar-nav">
                        <li className="nav-item">
                            <NavLink className="nav-link active" aria-current="page" onClick={handleLogout} to="/">Inicio</NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink className="nav-link" to="/Participantes">Participantes</NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink className="nav-link" to="/Registro">Registro</NavLink>
                        </li>
                    </ul>
                </div>
                <img  src={logo} className="logo" />

            </div>
        </nav>
    )
}

export default Navbar;
