import React from 'react';
import {Link} from 'react-router-dom';
import icon from '../img/icon.png';

const Card = ({ participante} ) =>{
    

    return (
       
        <div className="card mb-3" style={{ width: 540 }}>                
        <div className="row g-0">
            <div className="col-md-3"><br></br>
            <Link to={"update/"+ participante.id} > <img src={icon} className="img-fluid rounded-start" alt="..." style={{ width: 80, padding:10 }}  /></Link>
            </div>                
            <div className="col-md-8">
              <div className="card-body" >
                    <h6 className="card-title">{participante.nombre} {participante.apellidos}</h6>
                   
                   <i class="fa fa-twitter"/> <a href={`https://twitter.com/${participante.twitter}`} className="card-text">{participante.twitter}</a> 
                    <h6 className="card-title">{participante.ocupacion}</h6>
                   <br></br>
               
                </div>                     
            </div> 

        </div>
    </div>
       
    );
};

export default Card;
