import React, { useEffect, useReducer, useState } from 'react';
import { AuthReducer } from '../reducers/AuthReducer';
import { useHistory } from 'react-router';
import {v4 as uuid} from "uuid";
import { Link } from 'react-router-dom';

const init = () => {

    const participantes = localStorage.getItem("participantes");
    return participantes ? JSON.parse(participantes) : [];
}  




const Registro = () => {

    const [state, dispatch] = useReducer(AuthReducer, [], init);
    const [formView, setformView] = useState(false);

    //Agregamos un state.
    const [data, setData] = useState({nombre: "", apellidos:"", correo:"", twitter:"", ocupacion:"" });
    //Desestructurarmos el state
    const {nombre,apellidos, correo, twitter, ocupacion} = data;

    //Método handleChange
    const handleChange = (e) =>{
        //Modificamos el estado
        setData({
            ...data,
            [e.target.name]: e.target.value,
        });
        
    };

    useEffect(() => {
        localStorage.setItem("participantes", JSON.stringify(state));
        }, [state]);
        

    //Definimos la acción para Add.
    const actionAdd = {
        type: "add",
        payload: {
            id: uuid(),
            nombre,          
            apellidos,
            correo,
            twitter, 
            ocupacion,        
        },
    };

    const history = useHistory();

    const handleAdd = () => {


        //Invocamos el dispatch, todos los dispatch tiene una referencia con el reducer a ejecutar, debemos de pasarle un action.
        if(!data.nombre || !data.apellidos || !data.correo || !data.twitter || !data.ocupacion ){
            alert("Todos los campos son obligatorios");
            return;
        }

        dispatch(actionAdd);
        handleAdd.reset();

        history.push("/Participantes");
    };



    return (
        <form >
               <br></br>   <br></br>   <br></br>
         <div className="container-sm col-md-6">      
            <label className="mx-1 d-grid gap-2">
                Nombre:{" "}
                <input 
                onChange={handleChange}
                name="nombre"
                 value={nombre} 
                type="text" 
                className="form-control" 
                autoComplate="off"
               />
            </label>
            <label className="mx-1 d-grid gap-2">
               Apellidos:{" "}
                <input 
                onChange={handleChange} 
                name="apellidos"
                 value={apellidos} 
                type="text" 
                className="form-control" 
                autoComplate="off"/>
            </label>
            <label className="mx-1 d-grid gap-2">
               Email:{" "}
                <input 
                onChange={handleChange} 
                name="correo"
                 value={correo} 
                type="text" 
                className="form-control" 
                autoComplate="off"/>
            </label>
            <label className="mx-1 d-grid gap-2">
               Twitter:{" "}
                <input 
                onChange={handleChange} 
                name="twitter" 
                value={twitter} 
                type="text" 
                className="form-control" 
                autoComplate="off"/>
            </label>
            <label className="mx-1 d-grid gap-2">
               Ocupación:{" "}
                <input 
                onChange={handleChange} 
                name="ocupacion" 
                value={ocupacion} 
                type="text" 
                className="form-control" 
                autoComplate="off"/>
            </label> <br></br>
            <label><input type="checkbox" id="cbox1" value="first_checkbox"/> Leí y acepto los términos y condiciones</label>
            
            <div className="mx-1 d-grid gap-2">
                <button  onClick={handleAdd} className="btn btn-success mt-2">Guardar</button>
            </div> 
       </div>

        </form>
    );
};

export default Registro;
