import React, { useReducer } from 'react';
import { useHistory } from 'react-router';
import Card from '../components/Card';
import { AuthReducer } from '../reducers/AuthReducer';


const init = () => {

    const participantes = localStorage.getItem("participantes");
    return participantes ? JSON.parse(participantes) : [];
} 



const ListaParticipantes = () => {

    const [state, dispatch] = useReducer(AuthReducer, [], init);

    const history = useHistory();

    const handleRegister = () => {

        history.push("/Registro");
    }


    return (
        <>
         <br></br>
        <div className="container-sm col-md-6">    
                <div className="d-grid gap-2 d-md-flex justify-content-md-end">
                    <button onClick={handleRegister} className="btn btn-success ">Agregar</button>
                </div>    <br></br>  
     
                <div className="row g-0">
                       
                    {
                         state.map((participante) => (
                            <Card key={participante.id} participante={participante} />
                        ))            
                    }
                 </div>
               
        </div>
        </>
    );
};

export default ListaParticipantes;
