import React, { useReducer } from 'react';
import {withRouter} from 'react-router-dom';
import {useState, useEffect} from 'react';
import {v4 as uuid} from "uuid";
import { AuthReducer } from '../reducers/AuthReducer';

const init = () => {

    const participantes = localStorage.getItem("participantes");
    return participantes ? JSON.parse(participantes) : [];
}  


const Editar3 = (props) => {
    const [state, dispatch] = useReducer(AuthReducer, [], init);

    const [data, setData] = useState([]);
    // console.warn("props",props.match.params.id);

    // useEffect(() => {
    //     localStorage.setItem("participantes"+ props.match.params.id, JSON.stringify(state));
    // }, [state]);


    return (
        <form >
               <br></br>   <br></br>   <br></br>
         <div className="container-sm col-md-6">      
            <label className="mx-1 d-grid gap-2">
                Nombre:{" "}
                <input 
                name="nombre"
                defaultValue={data.nombre} 
                type="text" 
                className="form-control" 
                autoComplate="off"
               />
            </label>
            <label className="mx-1 d-grid gap-2">
               Apellidos:{" "}
                <input 
               
                name="apellidos"
                defaultValue={data.apellidos} 
                type="text" 
                className="form-control" 
                autoComplate="off"/>
            </label>
            <label className="mx-1 d-grid gap-2">
               Email:{" "}
                <input 
                
                name="correo"
                defaultValue={data.correo} 
                type="text" 
                className="form-control" 
                autoComplate="off"/>
            </label>
            <label className="mx-1 d-grid gap-2">
               Twitter:{" "}
                <input                
                name="twitter" 
                defaultValue={data.twitter} 
                type="text" 
                className="form-control" 
                autoComplate="off"/>
            </label>
            <label className="mx-1 d-grid gap-2">
               Ocupación:{" "}
                <input 
                name="ocupacion" 
                defaultValue={data.ocupacion} 
                type="text" 
                className="form-control" 
                autoComplate="off"/>
            </label> <br></br>
            
            
            <div className="mx-1 d-grid gap-2">
                <button  className="update" className="btn btn-success mt-2">Editar</button>
            </div> 
       </div>

        </form>
    )
}

export default withRouter(Editar3);
