import React from 'react';
import { Switch, Route, Redirect } from 'react-router-dom';
import Navbar from '../components/Navbar';
//import Editar from '../pages/Editar';
import Editar2 from '../pages/Editar2';
import Editar3 from '../pages/Editar3';
import ListaParticipantes from '../pages/ListaParticipantes';
import Registro from '../pages/Registro';


const AppRouter = () => {
    return (
        <>
            <Navbar />
            <Switch>
                <Route exact path="/Participantes" component={ListaParticipantes} />
                <Route exact path="/Registro" component={Registro} />
                {/* <Route exact path="/Editar" component={Editar2} /> */}
                {/* <Route exact path="/update/:id" component={Editar2} /> */}
                <Route exact path="/update/:id" component={Editar3} />
                
                {/*Ruta con parametro (:id) */} 
                {/* <Route exact path="/character/:id" component={CharacterScreen} /> */}

                {/*Con <Redirect/> hacemos que la ruta / nos redirija a /mens*/}
                <Redirect to="/" />
            </Switch>
        </>
    );
};

export default AppRouter;
