import { authTypes } from "../types/authTypes";

export const AuthReducer = (state, action) => {
    switch (action.type) {
        case "add":
            return [...state, action.payload] //Hacemos una copia del state y agregamos un action.payload (payload contiene la información con los nuevos datos)

        // case "update":
        //     return state.filter((actual) => actual-id !== action.payload);

        case authTypes.login:
            return { log: true };
        case authTypes.logout:
            return { log: false };
        default:
            return state;
    }
};