import React from 'react';
import { Redirect, Route } from 'react-router';

//component: Representa el componente que vamos a renderizar.
const PrivateRouter = ({ auth, component: Component, ...rest }) => {
    return <Route 
        {...rest} 
        //Protegemos la ruta verificando el valor de auth.
        component={(props) => auth.log ? <Component {...props} /> : <Redirect to="/login" /> }  />
};

export default PrivateRouter;
