import React, { useContext } from 'react';
import {BrowserRouter as Router, Switch} from 'react-router-dom';
import { AuthContext } from '../context/AuthContext';
import Inicio from '../pages/Inicio';
import AppRouter from './AppRouter';
import PrivateRouter from './PrivateRouter';
import PublicRouter from './PublicRouter';

const LoginRouter = () => {

    //Recuperamos el context mediante el hook useContext para así poder saber si el usuario esta autenticado o no.
    const { log } = useContext(AuthContext);


    return (
        <Router>
         { /*  <AppRouter/>*/}
            {/*  <Route exact path="/login" component={LoginScreen} />*/}
            <Switch>              
               <PublicRouter path="/login" auth={log} component={Inicio} />
               <PrivateRouter path="/" auth={log} component={AppRouter} />
            </Switch>
            
        </Router>
    );
};

export default LoginRouter;
